<template>
  <div >

    <div class="mt-6" style="display: flex; align-items: center; justify-content: center;">
      <h1 >
        Welcome Back!
      </h1>
    </div>
   <v-row class="py-6 px-6">
    <v-col :cols="12">
      <v-text-field label="Email" prepend-inner-icon="mdi-email" filled v-model="email" block outlined></v-text-field>
    </v-col>

    <v-col :cols="12">
      <v-text-field label="Password" prepend-inner-icon="mdi-lock" filled v-model="password" block outlined type="password"></v-text-field>
    </v-col>


    <v-col :cols="12">
      <v-btn block :color="getPrimaryColor" dark @click="login()" x-large><v-icon>mdi-account-open</v-icon>Login</v-btn>
    </v-col>
   </v-row>
  </div>
</template>

<script>

export default{
  computed:{
    isMobile(){
      return this.$store.getters.isMobile
    },
    getPrimaryColor(){
      return this.$store.getters.getPrimaryColor
    },
    getSecondaryColor(){
      return this.$store.getters.getSecondaryColor
    },
    getTertiaryColor(){
      return this.$store.getters.getTertiaryColor
    },
  },
  data:()=>({
    email:null,
    password:null
  }),
  methods:{
    login(){
      if(!this.email || !this.password){
        this.$toast.error("Email and Password Required!")
        return false;
      }

      this.$store.commit("setAuthenticated",true)
      this.$router.push({path:'/admin-dashboard'})
    }
  }
}
</script>
